* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Calibri', sans-serif;
  font-weight: bold;
  font-size: 20px;
  color: #3f3f3f;
}
ul {
  list-style-type: none;
}
button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}
body {
  height: 100vh;
  width: 100vw;
  padding: 50px;
  background: linear-gradient(90deg, #0f343e, #1f0c31);
}
.content_block {
  background-color: white;
  padding: 25px;
  border-radius: 10px;
  box-shadow: 2px 2px 5px black;
  margin-bottom: 25px;
}
.switcher {
  width: 100%;
}
.switcher_icon {
  transform: rotate(-90deg);
  transition: 0.5s;
}
.switcher_icon_off {
  transform: rotate(90deg);
}
#change_message {
  width: 100%;
  border-radius: 5px;
  padding: 10px;
  border: 2px solid #d3d3d3;
  color: #9d9d9d;
  font-size: 18px;
  margin-bottom: 25px;
}
